import React, { useEffect, useState } from 'react'
import {Title,BackgroundAddArchievement,NoteStyle,InputForm,ReturnButton, ButtonAdd } from './RecentArchievementStyle';
import { v4 as uuidv4 } from 'uuid';
export default function AddArchievement() {
    const getLocal =()=>{
        let list=localStorage.getItem('allArchievement');
        if (list){
            return JSON.parse(list);
        }
        else return [];
    }
    const [archievement,setArchievement]=useState({id:uuidv4(),distance:"",totalTime:"",date:"",start:"",end:"",comment:"",
    height:"",weight:"",imageLink:""});
    const [allArchievement,setAllArchievement]=useState(getLocal());
    const handleAddArchievement=()=>{
        setAllArchievement([...allArchievement,archievement]);
        setArchievement({id:uuidv4(),distance:"",totalTime:"",date:"",start:"",end:"",comment:"",height:"",weight:"",imageLink:""});
    }
    useEffect(()=>{
        localStorage.setItem('allArchievement',JSON.stringify(allArchievement));
    },[allArchievement]);
    return (
        <BackgroundAddArchievement>
            <Title className='pt-4'>Thm Thnh Tch</Title>
            <form>
            <div className='container'>

            <div className='row'>
                <div className='col-md-5 col-xs-12'>
                    <InputForm type="text" className="form-control mt-5" placeholder="Qung ng" onChange={(e)=>setArchievement({ ...archievement,distance:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>n v: km</NoteStyle>
                    <InputForm type="text" className="form-control mt-5" placeholder="Thi Gian Chy" onChange={(e)=>setArchievement({ ...archievement,totalTime:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>n v: Pht</NoteStyle>
                    <InputForm type="date" className="form-control mt-5" placeholder="Ngy Chy" onChange={(e)=>setArchievement({ ...archievement,date:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>(Thng-Ngy-Nm)</NoteStyle>
                    <InputForm type="text" className="form-control mt-5" placeholder="Chiu Cao" onChange={(e)=>setArchievement({ ...archievement,height:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>n v: Cm</NoteStyle>
                </div>
                <div className='col-md-2'></div>
                <div className='col-md-5 col-xs-12'>
                    <InputForm type="text" className="form-control mt-5" placeholder="Cn Nng" onChange={(e)=>setArchievement({ ...archievement,weight:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>n v: Kg</NoteStyle>
                    <InputForm type="text" className="form-control mt-5" placeholder="a im Bt u" onChange={(e)=>setArchievement({ ...archievement,start:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>V d: Phm Hng</NoteStyle>
                    <InputForm type="text" className="form-control mt-5" placeholder="a im Kt Thc" onChange={(e)=>setArchievement({ ...archievement,end:e.target.value})}/>
                    <NoteStyle className='float-left pl-1'>V d: Khut Duy Tin</NoteStyle>
                    <textarea type="text" className="form-control mt-5" placeholder="M T" style={{height:"65px", fontSize:"25px"}} onChange={(e)=>setArchievement({ ...archievement,comment:e.target.value})}/>
                    <NoteStyle className='float-left pl-1 mb-1'>V d: Thi gian chy cha </NoteStyle>
                </div>
            </div>
            <div className='row'>
                <div className='col-2'></div>
                <div className='col-md-12 col-xs-12'>
                    <InputForm type="text" className="form-control mb-1 mt-1" placeholder="Link nh" onChange={(e)=>setArchievement({ ...archievement,imageLink:e.target.value})}/>
                    <ButtonAdd type="reset" className='form-control btn btn-outline-info mt-2' onClick={handleAddArchievement}> <i className="fas fa-save"> </i> Lu Li</ButtonAdd>
                    <ReturnButton href='/thanh-tich' className='btn btn-outline-primary form-control mt-4 mb-3'><i className="fa fa-undo"></i> Tr V</ReturnButton>
                </div>
            </div>
            </div>
            </form>
        </BackgroundAddArchievement>      
    )
}
